.App{
    min-height: 100vh;
    background-image: linear-gradient(135deg, #fdfcfb 0%, #e2d1c3 100%);
}

.search-form{
    min-height: 10vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.search-bar{
    width:30%;
    border: none;
    padding:10px;
}

.search-button{
    background: lightcoral;
    border:none;
    padding: 10px 20px;
    color:white;
}

.recipes{
    display:flex;
    flex-wrap: wrap;
    justify-content: space-around;
}